body{
    box-sizing: border-box !important;
}
input,textarea{
    background-color: rgba(255,255,255,0.35);
}
.slideRight{
    animation: slideRight 0.5s ease forwards;
}
.slideLeft{
    animation: slideLeft 0.5s ease forwards;
}
.slideMoreRight{
    animation: slideMoreRight 0.7s ease forwards;
}
.slideMoreLeft{
    animation: slideMoreLeft 0.7s ease forwards;
}
.Flash{
    animation: Flash 1.5s ease infinite;
}
@keyframes slideMoreRight{
    0%{
        transform: translateX(-200px);
        opacity: 0;
    }
    50%{
        transform: translateX(130px);
        opacity: 0.6;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes slideMoreLeft{
    0%{
        transform: translateX(200px);
        opacity: 0;
    }
    50%{
        transform: translateX(-130px);
        opacity: 0.6;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes slideRight{
    0%{
        transform: translateX(-100px);
        opacity: 0;
    }
    50%{
        transform: translateX(80px);
        opacity: 0.6;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes slideLeft{
    0%{
        transform: translateX(100px);
        opacity: 0;
    }
    50%{
        transform: translateX(-80px);
        opacity: 0.6;
    }
    100%{
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes Flash{
    0%{
        box-shadow: 0px 0px 6px 0px white;
    }
    100%{
        box-shadow: 0px 0px 0px 0px white;
    }
}
.swiper-slide{
    width: 90vw !important;
    max-width: 700px !important;
};
@media screen and (min-width:768px) {
    .swiper-slide{
        width: 40vw !important;
    };
};
.swiper{
    max-width: 1240px !important;margin: auto
};
.customBox{
    box-sizing: border-box !important;
}
